* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  font-family: Arial, sans-serif;
  color: #f4f4f4;
  background-color: #333;
}

html {
  overflow-y: scroll; /* Forces scrollbar always present */
}

.container {
  width: 50%;
  margin: 0 auto;
  text-align: center;
  padding: 20px;
  line-height: 1.5;
}